import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

import * as THREE from "three";

import { Suspense , useRef, useState, useMemo, useEffect } from 'react';

import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

const ThreePage = () => {

  const data = useStaticQuery(
    graphql`
      query {
        bg1:

        file(relativePath: { eq: "pointcloud.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

        bg2:
        file(relativePath: { eq: "obj.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }

      }
    `
  )

  const bg1 = data.bg1.childImageSharp.fluid
  const bg2 = data.bg2.childImageSharp.fluid

const Vis = () => {
  const { useRef, useEffect, useState } = React
  const mount = useRef(null)
  const [isAnimating, setAnimating] = useState(true)
  const controls = useRef(null)
  
  useEffect(() => {
    let width = 1000
    let height = 1000
    let frameId

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ antialias: true })
    const geometry = new THREE.BoxGeometry(1, 1, 1)
    const material = new THREE.MeshBasicMaterial({ color: 0xff00ff })
    const cube = new THREE.Mesh(geometry, material)

    const loader = new GLTFLoader();

    // loader.load(
    //   // resource URL
    //   '/3dcdll.glb',
    //   // called when the resource is loaded
    //   function ( gltf ) {

    //     scene.add( gltf.scene );

    //     // gltf.animations; // Array<THREE.AnimationClip>
    //     // gltf.scene; // THREE.Group
    //     // gltf.scenes; // Array<THREE.Group>
    //     // gltf.cameras; // Array<THREE.Camera>
    //     // gltf.asset; // Object

    //   },
    //   // called while loading is progressing
    //   function ( xhr ) {

    //     console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

    //   },
    //   // called when loading has errors
    //   function ( error ) {

    //     console.log( 'An error happened' );

    //   }
    // );    

    camera.position.z = 4
    scene.add(cube)
    renderer.setClearColor('#000000')
    renderer.setSize(width, height)

    const renderScene = () => {
      renderer.render(scene, camera)
    }

    const handleResize = () => {
      width = mount.current.clientWidth
      height = mount.current.clientHeight
      renderer.setSize(width, height)
      camera.aspect = width / height
      camera.updateProjectionMatrix()
      renderScene()
    }
    
    const animate = () => {
      // cube.rotation.x += 0.01
      // cube.rotation.y += 0.01

      renderScene()
      frameId = window.requestAnimationFrame(animate)
    }

    const start = () => {
      if (!frameId) {
        frameId = requestAnimationFrame(animate)
      }
    }

    const stop = () => {
      cancelAnimationFrame(frameId)
      frameId = null
    }

    mount.current.appendChild(renderer.domElement)
    window.addEventListener('resize', handleResize)
    start()

    controls.current = { start, stop }
    
    return () => {
      stop()
      window.removeEventListener('resize', handleResize)
      mount.current.removeChild(renderer.domElement)

      scene.remove(cube)
      geometry.dispose()
      material.dispose()
    }
  }, [])

  useEffect(() => {
    if (isAnimating) {
      controls.current.start()
    } else {
      controls.current.stop()
    }
  }, [isAnimating])
  
  return <div className="vis h-100" ref={mount} onClick={() => setAnimating(!isAnimating)} />
}

  return (
    <Layout>
      <Seo title="Home" />

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid fullheight-ribbon d-flex flex-column'}
          fluid={bg1}
          backgroundColor={`#040e18`}
        >

            <div className={`row flex-1`}>
              <div className={`col flex-1`}>


              <Vis />

              </div>         
            </div>
            <div className={`row ribbon no-gutters`}>

            	<div className={`col-3 ribbon-blue`}></div>
            	<div className={`col-2 ribbon-red`}></div>
            	<div className={`col-4 ribbon-navy`}></div>
            	<div className={`col-3 ribbon-gray`}></div>

            </div>

        </BackgroundImage>

      </FullScreenSection>   

    </Layout>
  )

}

const StyledBackgroundSection = styled(ThreePage)`
  width: 100%;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;
`

export default ThreePage
